import { BigNumber } from 'bignumber.js'
import BN from 'bn.js'
import Config from '../utils/config'

const gasLimit = 2400000

const getComptroller = async (web3, networkType) => {

    const contractABI = Config.comptroller.ABI
    const contractAddress = Config.comptroller.network[networkType].address
    const contract = await new web3.eth.Contract(contractABI, contractAddress)
    return contract

}

const getTokenContract = async (web3, networkType, symbol) => {
    const contractABI = Config.markets[symbol].ABI;

    const theNetwork = Config.markets[symbol].network[networkType]
    if (!theNetwork) {
        return null
    }

    const contractAddress = theNetwork.address;
    let contract = await new web3.eth.Contract(contractABI, contractAddress);
    return contract
}

const getQTokenContract = async (web3, networkType, symbol) => {
    const qContractABI = Config.markets[symbol].qToken.ABI;

    const theNetwork = Config.markets[symbol].qToken.network[networkType]
    if (!theNetwork) {
        return null
    }

    const qContractAddress = theNetwork.address;
    let qContract = await new web3.eth.Contract(qContractABI, qContractAddress);
    return qContract
}

const getTokenAddress = (networkType, symbol) => {
    if (Config.WNATIVE.symbol.includes(symbol)) {
        return Config.WNATIVE.network[networkType]
    }
    return Config.markets[symbol].network[networkType].address
}

const getQTokenAddress = (networkType, symbol) => {
    return Config.markets[symbol].qToken.network[networkType].address
}

const getGasInfo = async (web3) => {
    const gasPrice = await web3.eth.getGasPrice()

    return {
        gasPrice: +BigNumber(gasPrice).plus(web3.utils.unitMap.gwei),
        gasLimit: gasLimit
    }
}

const recomputeGas = async (method, params, gasInfo, networkType) => {
    const gas = await method.estimateGas(params);
    if (gas < Config.headerConfigData.network[networkType].normalGasLimit) {
        params.gas = gasInfo.gasLimit;
    } else {
        params.gas = gas;
    }
    return params;
}

const getExplorerUrl = (txnHash, networkType) => {
    return Config.blockExplorers[networkType] + 'tx/' + txnHash
}

const getAddressUrl = (address, networkType, lpTokenId) => {
    let dexUrl = Config.pools[networkType]?.[address]?.dexUrl
    if (dexUrl) return dexUrl

    if (['FHT', 'FHUSD', 'FELA', 'FBUSD_b', 'FWIOTX', 'FILDA', 'ELK', 'FWELA'].includes(lpTokenId)) {
        return Config.mdexUrls[lpTokenId]
    }
    return `${Config.blockExplorers[networkType]}address/${address}`
}

const getShortAddress = (address) => {
    let addressStr = address.toString()
    return addressStr.slice(0, 5) + '...' + addressStr.slice((addressStr.length - 3) - addressStr.length)
}

const getRawValue = async (web3, networkType, symbol, value) => {
    let decimals = 18
    if (!isNativeToken(symbol, networkType)) {
        const contract = await getTokenContract(web3, networkType, symbol)
        if (!contract) {
            return new BigNumber(0)
        }

        decimals = await contract.methods.decimals().call()
    }
    BigNumber.config({ ROUNDING_MODE: BigNumber.ROUND_DOWN })
    return BigNumber(value).shiftedBy(parseInt(decimals)).toFixed(0)
}

function padLeft(num, nSize, ch) {
    var len = 0;
    var s = num ? num.toString() : "";
    ch = ch ? ch : '0';// 默认补0

    len = s.length;
    while (len < nSize) {
        s = ch + s;
        len++;
    }
    return s;
}

function padRight(number, nSize, ch) {
    var len = 0;
    var s = number;
    ch = ch ? ch : '0';// 默认补0

    len = s.length;
    while (len < nSize) {
        s = s + ch;
        len++;
    }
    return s;
}

function movePointRight(num, scale) {
    var s, s1, s2, ch, ps;
    ch = '.';
    s = num.toString();
    if (scale <= 0) return s;
    ps = s.split('.');
    s1 = ps[0] ? ps[0] : "";
    s2 = ps[1] ? ps[1] : "";
    if (s2.length <= scale) {
        ch = '';
        s2 = padRight(s2, scale, 0);
    }
    return s1 + s2.slice(0, scale) + ch + s2.slice(scale, s2.length);
}

function movePointLeft(num, scale) {
    var s, s1, s2, ch, ps, sign;
    ch = '.';
    sign = '';
    s = num ? num : "";
    if (scale <= 0) return s;
    ps = s.split('.');
    s1 = ps[0] ? ps[0] : "";
    s2 = ps[1] ? ps[1] : "";
    if (s1.slice(0, 1) === '-') {
        s1 = s1.slice(1);
        sign = '-';
    }
    if (s1.length <= scale) {
        ch = "0.";
        s1 = padLeft(s1, scale);
    }
    return sign + s1.slice(0, -scale) + ch + s1.slice(-scale) + s2;
}

function toFixed(x) {
    if (Math.abs(x) < 1.0) {
        var e = parseInt(x.toString().split('e-')[1]);
        if (e) {
            x *= Math.pow(10, e - 1);
            x = '0.' + (new Array(e)).join('0') + x.toString().substring(2);
        }
    } else {
        var e = parseInt(x.toString().split('+')[1]);
        if (e > 20) {
            e -= 20;
            x /= Math.pow(10, e);
            x += (new Array(e + 1)).join('0');
        }
    }
    return x;
}

function fixedNaN(number) {
    if (isNaN(number)) {
        return 0
    }
    return number
}

function fromWei(web3, number, unit) {
    const truncatedNumber = toFixed(Math.trunc(number))
    const bnTruncatedNumber = new BN(truncatedNumber.toString())
    return web3.utils.fromWei(bnTruncatedNumber, unit)
}

const getCompLens = async (web3, networkType) => {
    const contractABI = Config.compoundLens.ABI
    const contractAddress = Config.compoundLens.network[networkType].address
    const contract = await new web3.eth.Contract(contractABI, contractAddress)
    return contract
}

const getMaximillion = async (web3, networkType) => {
    const contractABI = Config.maximillion.ABI
    const contractAddress = Config.maximillion.network[networkType].address
    const contract = await new web3.eth.Contract(contractABI, contractAddress)
    return contract
}

function isNativeToken(symbol, network) {
    if ((network === Config.chainIdMap["128"] || network === Config.chainIdMap["256"]) && symbol === "HT") {
        return true
    }

    if ((network === Config.chainIdMap["56"] || network === Config.chainIdMap["97"]) && symbol === "BNB") {
        return true
    }
    
    if ((network === Config.chainIdMap["137"] || network === Config.chainIdMap["80001"]) && symbol === "Matic") {
        return true
    }

    if ((network === Config.chainIdMap["21"] || network === Config.chainIdMap["20"]) && symbol === "ELA") {
        return true
    }

    if ((network === Config.chainIdMap["4690"] || network === Config.chainIdMap["4689"]) && symbol === "IOTX") {
        return true
    }

    if ((network === Config.chainIdMap["421611"] || network === Config.chainIdMap["42161"]) && symbol === "ETH") {
        return true
    }

    if ((network === Config.chainIdMap["322"] || network === Config.chainIdMap["321"]) && symbol === "KCS") {
        return true
    }

    return false
}

function toUpperCase(lang, txt) {
    return lang === 'en' ? txt.toUpperCase() : txt;
}

function isAppDomain() {
    const appDomain = 'app.filda.io'
    // const appDomain = '192.168.72.156'
    return new URL(window.location).hostname.toLowerCase() === appDomain.toLowerCase()
}

const getApprovalMsgToken = (networkType) => {
    if (networkType) {
        return Config.approvalMsgToken[networkType] || 'ERC20'
    } else {
        return 'HRC20'
    }
}

const getLPSwapName = (networkType = 'heco') => {
    return Config.lpSwapName[networkType] || 'MDEX' 
}

export default {
    getComptroller: getComptroller,
    getTokenContract: getTokenContract,
    getQTokenContract: getQTokenContract,
    getTokenAddress: getTokenAddress,
    getQTokenAddress: getQTokenAddress,
    getGasInfo: getGasInfo,
    recomputeGas,
    getExplorerUrl: getExplorerUrl,
    getAddressUrl: getAddressUrl,
    getShortAddress: getShortAddress,
    getRawValue,
    fixedNaN,
    getCompLens,
    toFixed,
    getMaximillion,
    isNativeToken,
    fromWei,
    movePointRight,
    toUpperCase,
    isAppDomain,
    getApprovalMsgToken,
    getLPSwapName,
}
