import React, {useState, useEffect} from 'react'
import classNames from 'classnames'
import { useTranslation } from 'react-i18next'
import {OverlayTrigger, Tooltip} from 'react-bootstrap'


const TorTooltip = ({
  title = '',
  desc = '',
  type = 'line',
  tooltipClassName = '',
  isShow = false,
  ...props
}) => {
  const { t, i18n } = useTranslation()
  const [show, setShow] = useState(isShow)


  const showHandler = (e) => {
    if (e.target.className === 'tor-tooltip-infoIcon') {
      setShow(true)
    }
  }

  const hideHandler = (e) => {
    !isShow && setShow(false)
  }


  return !desc ? title : type === 'line' ? (
    <div 
      className={classNames('tor-tooltip-Line')} 
      onMouseOver={() => setShow(true)}
      onMouseLeave={hideHandler}
    >
      <OverlayTrigger
        show={show}
        overlay={
          <Tooltip 
            className={classNames('tor-tooltip', tooltipClassName)}
          >
            {desc}
          </Tooltip> 
        }
      >
        <span>{title}</span>
      </OverlayTrigger>
    </div>
  ) : (
    <div 
      className={classNames('tor-tooltip-Icon', {
        ['tor-tooltip-Icon-white']: props.iconColor === 'white'
      })} 
      onMouseOver={showHandler}
      onMouseLeave={hideHandler}
    >
      <span>{title}</span>
      <OverlayTrigger 
        show={show}
        overlay={
          <Tooltip 
            className={classNames('tor-tooltip', tooltipClassName)}
          >
            {desc}
          </Tooltip> 
        }
      >
        <span className={classNames('tor-tooltip-infoIcon')}></span>
      </OverlayTrigger>
    </div>
  )
  
}

export default TorTooltip
