import React, { useContext, useState, useEffect, useRef } from 'react'
import classNames from 'classnames'
import { useTranslation } from 'react-i18next'
import { isMobile } from 'react-device-detect'
import { throttle } from 'lodash-es'

export const ColorProgress = ({
  id,
  styles={},
  percent = 0, 
  isDrag = false,
  fdRef = '',
  percentHandler = () => {}
}) => {
  const { t, i18n } = useTranslation()
  const [activePercent, setActivePercent] = useState(percent)

  useEffect(() => {
    
    let _startHandler, _moveHandler, _endHandler
    const startEvent = isMobile ? 'touchstart' : 'mousedown'
    const moveEvent = isMobile ? 'touchmove' : 'mousemove'
    const endEvent = isMobile ? 'touchend' : 'mouseup'

    setActivePercent(percent)

    if (isDrag) {
      let startX = 0
      const el = document.getElementById(id)
      
      _startHandler = (evt) => {
        if (evt.target.getAttribute('class') === 'barCircle') {
          startX = isMobile ? evt.changedTouches[0].clientX : evt.clientX;
        }
      }

      const _calcCircleLeft = (evt) => {
        const domWidth = el.getBoundingClientRect().width
        const clientX = isMobile ? evt.changedTouches[0].clientX : evt.clientX
        let calcLeft = clientX - el.getBoundingClientRect().left
        if (calcLeft<0) {
          calcLeft = 0
        }
        if (calcLeft > domWidth) {
          calcLeft = domWidth
        }
        calcLeft = calcLeft / domWidth * 100
        
        return calcLeft
      }

      _moveHandler = throttle((evt) => {
        if (startX && el.contains(evt.target)) {
          const calcLeft = _calcCircleLeft(evt)
          setActivePercent(calcLeft)
          percentHandler('move', calcLeft)
        }
      }, 100)

      _endHandler = (evt) => {
        if (el.contains(evt.target)) {
          const calcLeft = _calcCircleLeft(evt)
          setActivePercent(calcLeft)
          percentHandler('end', calcLeft)          
        }
        startX = 0
      }

      document.addEventListener(startEvent, _startHandler)
      document.addEventListener(moveEvent, _moveHandler)
      document.addEventListener(endEvent, _endHandler)
    }

    return () => {
      if (isDrag) {
        document.removeEventListener(startEvent, _startHandler, { passive: false })
        document.removeEventListener(moveEvent, _moveHandler, { passive: false })
        document.removeEventListener(endEvent, _endHandler, { passive: false })
      }
    }

  }, [percent])

  const colorProgressProps = {
    className: 'colorProgress',
    style: styles
  }

  id && (colorProgressProps.id = id)

  fdRef && (colorProgressProps.ref = fdRef)

  return (
    <div 
      {...colorProgressProps}
    >
      <div 
        className="barInner" 
        style={{
          width: `${activePercent}%`,
          background: Number(activePercent) >= 100 ? '#FB6491' : Number(activePercent) >= 85 ? '#fbbe64' : '#3784F9'
        }}
      >
      </div>
      {isDrag && (
        <div 
          className="barCircle" 
          style={{left: activePercent + '%'}}
        ></div>
      )}
    </div>
  )
}